'use client'

import { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

export default function Footer() {
  const footerRef = useRef<HTMLElement>(null)

  useEffect(() => {
    gsap.fromTo(footerRef.current,
      { opacity: 0, y: 30 },
      {
        opacity: 1,
        y: 0,
        duration: 0.8,
        ease: 'power3.out',
        scrollTrigger: {
          trigger: footerRef.current,
          start: 'top 95%',
          toggleActions: 'play none none reverse',
        },
      }
    )
  }, [])

  return (
    <footer 
      ref={footerRef}
      className="px-6 md:px-12 py-8 border-t border-white/10 flex flex-col md:flex-row justify-between items-center gap-4"
    >
      <div className="font-mono text-sm text-[#666]">
        ¬© {new Date().getFullYear()} TheShvdow. Tous droits r√©serv√©s.
      </div>
      
      <div className="font-mono text-sm text-[#666]">
        Construit avec ‚ù§Ô∏è et{' '}
        <a 
          href="https://nextjs.org" 
          target="_blank" 
          rel="noopener noreferrer"
          className="text-accent hover:underline"
        >
          Next.js
        </a>
      </div>
    </footer>
  )
}
